:root {
  --blue: #2596be;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title {
  font-weight: bold;
  text-decoration: none;
  color: #2596be;
  color: var(--blue);
  font-size: 36px;
}

.title:hover {
  color: #fff;
  background-color: #2596be;
  background-color: var(--blue);
}

.title.active {
  color: #2596be;
  color: var(--blue);
}

.link {
  padding-top: 20px;
  color: #2596be;
  color: var(--blue);
  text-decoration: none;
  font-size: 24px;
}

.link:hover {
  color: #2596be;
  color: var(--blue);
  font-weight: bold;
}

.link.active {
  color: #2596be;
  color: var(--blue);
}

ul {
  list-style-type: none;
}

.section {
  margin: 0 auto;
  text-align: center;
  background-color: #2596be;
  background-color: var(--blue);
  padding: 25px;
  color: #fff;
  width: 80%;
}

.footer {
  background-color: #2596be;
  background-color: var(--blue);
  color: #fff;
  font-size: 32px;
  padding: 40px;
  text-align: center;
  position: relative; 
  bottom: 0; 
  width: 100%
}

.manyForms {
  background-color: #2596be;
  background-color: var(--blue);
  color: #fff;
  text-align: center;
  padding: 50px;
}

.break {
  background-color: #2596be;
  background-color: var(--blue);
  border-color: #2596be;
  border-color: var(--blue);
  border: #fff;
  padding: 50px;
}

.blank {
  padding: 10px;
  background-color: #fff;
  border-color: #fff;
}

button {
  font-size: 15px;
  text-decoration: none;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 25px;
  padding-right: 25px;
  border: none;
  outline: none;
  cursor: pointer;
  background-color: #000;
  color: #fff;
}

button:hover {
  color: #2596be;
  color: var(--blue);
  background-color: #fff;
  font-weight: bold;
  border: 1px solid #2596be;
  border: 1px solid var(--blue);
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 24px;
  padding-right: 24px;
}

button[disabled] {
  cursor: default;
  opacity: 0.8;
}

.social {
  padding: 40px;
  display: flex;
  justify-content: space-around;
}

.socialMedia:hover {
  opacity: 0.5;
}

.text {
  color: #2596be;
  color: var(--blue);
}

.shop {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media screen and (max-width: 1030px) {
  .link {
    font-size: 16px;
    padding-left: 5px;
    padding-right: 5px;
  }

  .title {
    font-size: 28px;
  }
}

@media screen and (max-width: 380px) {
  .link {
    font-size: 12px;
  }

  .title {
    font-size: 20px;
  }
  
  .socialMedia {
    padding: 2px;
  }
}

@media screen and (max-width: 280px) {
  .link {
    font-size: 10px;
  }

  .title {
    font-size: 15px;
  }

  .section {
    font-size: 15px;
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
  --red: #cb2b2b;
}

.map {
  padding: 20px;
  display: flex;
  justify-content: center;
}

.google-map {
  width: 80%;
  height: 60vh;
}

.pin {
  display: flex;
  align-items: center;
  width: 180px;
  color: #cb2b2b;
  color: var(--red);
}

.pin-icon {
  font-size: 4rem;
}

.pin-text {
  font-size: 1.3em;
}

@media screen and (min-width: 799px) {
  .google-map {
    height: 80vh;
  }

  .pin {
    width: 15vw;
  }

  .pin-icon {
    font-size: 10vw;
  }
}
:root {
  --blue: #2596be;
}

.cart {
  position: fixed;
  top: 17%;
  right: 5%;
  min-width: 20%;
  max-width: 30%;
  max-height: 60%;
  background-color: #fff;
  overflow: auto;
  padding: .5rem;
  box-shadow: 0 0 1rem #2596be;
  box-shadow: 0 0 1rem var(--blue);
  border-bottom-left-radius: .5rem;
}

.cart .close {
  position: absolute;
  top: .5rem;
  right: .5rem;
  cursor: pointer;
  color: #2596be;
  color: var(--blue);
}

.cart .close:hover {
  text-decoration: underline;
}

.cart-closed {
  position: fixed;
  top: 17%;
  right: 5%;
  font-size: 2rem;
  cursor: pointer;
  background-color: #fff;
  border-radius: 50%;
  padding: .25rem;
  width: 50px;
  height: 50px;
}

.cart-closed:hover {
  -webkit-transform: rotate(8deg);
          transform: rotate(8deg);
}

.cart img {
  width: 70px;
  margin-right: 1rem;
}

.cart input {
  width: 50px;
  padding: 0;
  margin: 0 .5rem 1rem 0;
}

@media screen and (max-width: 900px) {
  .cart {
    display: flex;
    justify-content: center;
    padding-top: 5px;
    position: relative;
    max-width: 100%;
    box-shadow: none;
    background-color: inherit; 
  }

  .cart-closed {
    position: relative;
    margin: 0 auto;
  }
}

@media screen and (max-width: 410px) {
  .cart {
    font-size: 12px;
  }
}

@media screen and (max-width: 290px) {
  .cart {
    font-size: 8px;
  }
}
